import React, { useState } from 'react';
import { Role, Permission } from '../../../types/role';
import PermissionMatrix from './PermissionMatrix';

interface RoleEditorModalProps {
  isOpen: boolean;
  onClose: () => void;
  role: Role | null;
}

/**
 * Modal for creating or editing a role and assigning permissions.
 */
const RoleEditorModal: React.FC<RoleEditorModalProps> = ({ isOpen, onClose, role }) => {
  const [name, setName] = useState(role?.name || '');
  const [description, setDescription] = useState(role?.description || '');
  const [permissions, setPermissions] = useState<Permission[]>(role?.permissions || []);

  // Reset state when role changes
  React.useEffect(() => {
    setName(role?.name || '');
    setDescription(role?.description || '');
    setPermissions(role?.permissions || []);
  }, [role]);

  const handleSave = () => {
    // TODO: Integrate with API
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-40">
      <div className="bg-white dark:bg-gray-900 rounded-lg shadow-lg p-6 w-full max-w-lg" role="dialog" aria-modal="true">
        <h3 className="text-lg font-bold mb-4">{role ? 'Edit Role' : 'Create Role'}</h3>
        <div className="mb-4">
          <label htmlFor="role-name" className="block mb-1 font-medium">Role Name</label>
          <input
            id="role-name"
            className="w-full border rounded px-3 py-2"
            value={name}
            onChange={e => setName(e.target.value)}
          />
        </div>
        <div className="mb-4">
          <label htmlFor="role-description" className="block mb-1 font-medium">Description</label>
          <input
            id="role-description"
            className="w-full border rounded px-3 py-2"
            value={description}
            onChange={e => setDescription(e.target.value)}
          />
        </div>
        <div className="mb-4">
          <label className="block mb-1 font-medium">Permissions</label>
          <PermissionMatrix permissions={permissions} setPermissions={setPermissions} />
        </div>
        <div className="flex justify-end gap-2">
          <button className="px-4 py-2 rounded bg-gray-200" onClick={onClose}>Cancel</button>
          <button className="px-4 py-2 rounded bg-blue-600 text-white" onClick={handleSave}>
            Save
          </button>
        </div>
      </div>
    </div>
  );
};

export default RoleEditorModal;
